
/* from bootstrap 5 */
$navbar-brand-margin-end:           1rem !default;
$input-btn-focus-width:         .25rem !default;
$btn-focus-width:             $input-btn-focus-width !default;
$navbar-toggler-focus-width:        $btn-focus-width !default;
$navbar-toggler-transition:         box-shadow .15s ease-in-out !default;
$nav-link-font-size:                null !default;
$nav-link-font-weight:              null !default;
$nav-link-color:                    $link-color !default;
$nav-link-hover-color:              $link-hover-color !default;
$nav-link-transition:               color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out !default;
$card-title-spacer-y:               $spacer / 2 !default;
$card-cap-padding-y:                $card-spacer-y / 2 !default;
$card-cap-padding-x:                $card-spacer-x !default;
$table-striped-color:         $table-color !default;
$table-striped-bg-factor:     .05 !default;
$table-striped-bg:            rgba($black, $table-striped-bg-factor) !default;

/* navigation */
$sitelinks-navbar-padding-y:                  $navbar-padding-y !default;
$sitelinks-navbar-padding-x:                  $navbar-padding-x !default;

$sitelinks-navbar-nav-link-padding-x:         $navbar-nav-link-padding-x !default;

$sitelinks-navbar-brand-font-size:            $navbar-brand-font-size !default;
// Compute the navbar-brand padding-y so the navbar-brand will have the same height as navbar-text and nav-link
$sitelinks-nav-link-height:                   $nav-link-height !default;
$sitelinks-navbar-brand-height:               $navbar-brand-height !default;
$sitelinks-navbar-brand-padding-y:            $navbar-brand-padding-y !default;
$sitelinks-navbar-brand-margin-end:           $navbar-brand-margin-end !default;

$sitelinks-navbar-toggler-padding-y:          $navbar-toggler-padding-y !default;
$sitelinks-navbar-toggler-padding-x:          $navbar-toggler-padding-x !default;
$sitelinks-navbar-toggler-font-size:          $navbar-toggler-font-size !default;
$sitelinks-navbar-toggler-border-radius:      $navbar-toggler-border-radius !default;
$sitelinks-navbar-toggler-focus-width:        $navbar-toggler-focus-width !default;
$sitelinks-navbar-toggler-transition:         $navbar-toggler-transition !default;

/* links */
$sitelinks-nav-link-padding-y:                $nav-link-padding-y !default;
$sitelinks-nav-link-padding-x:                $nav-link-padding-x !default;
$sitelinks-nav-link-font-size:                $nav-link-font-size !default;
$sitelinks-nav-link-font-weight:              $nav-link-font-weight !default;
$sitelinks-nav-link-color:                    $nav-link-color !default;
$sitelinks-nav-link-hover-color:              $nav-link-hover-color !default;
$sitelinks-nav-link-transition:               $nav-link-transition !default;
$sitelinks-nav-link-disabled-color:           $nav-link-disabled-color !default;

/* theme */
$sitelinks-navbar-link-color:                 $navbar-dark-color;
$sitelinks-navbar-link-hover-color:           $navbar-dark-hover-color !default; 

$sitelinks-navbar-link-active-color:          $navbar-dark-active-color !default;
$sitelinks-navbar-dark-disabled-color:        $navbar-dark-disabled-color !default;
$sitelinks-navbar-dark-toggler-icon-bg:       $navbar-dark-toggler-icon-bg !default;
$sitelinks-navbar-dark-toggler-border-color:  $navbar-dark-toggler-border-color !default;
$sitelinks-navbar-dark-brand-color:           $navbar-dark-active-color !default;
$sitelinks-navbar-dark-brand-hover-color:     $navbar-dark-active-color !default;

/* e107 */
$sitelinks-navbar-bg: $primary !default;
$sitelinks-navbar-link-font-size:                $nav-link-font-size !default;
$sitelinks-navbar-link-font-weight:              $nav-link-font-weight !default;
 
$sitelinks-navbar-item-hover-color:           $navbar-dark-hover-color !default;  /* li bgground */
$sitelinks-navbar-item-padding-top:  10px !default;
$sitelinks-navbar-item-padding-bottom:  10px !default;
$sitelinks-navbar-item-hover-margin-top: -10px !default;
$sitelinks-navbar-item-hover-margin-bottom: -10px !default; 

$sitelinks-navbar-link-padding-top:  10px !default;
$sitelinks-navbar-link-padding-bottom:  10px !default;
$sitelinks-navbar-link-hover-margin-top: -10px !default;
$sitelinks-navbar-link-hover-margin-bottom: -10px !default; 

/* dropdown */
$sitelinks-dropdown-border-width: $dropdown-border-width!default;
$sitelinks-dropdown-border-color: $dropdown-border-color!default;


/*  cards */
$forum-card-spacer-y:                     $card-spacer-y !default;
$forum-card-spacer-x:                     $card-spacer-x !default;
$forum-card-title-spacer-y:               $card-title-spacer-y !default;
$forum-card-border-width:                 $card-border-width !default;
$forum-card-border-radius:                $card-border-radius !default;
$forum-card-border-color:                 $card-border-color !default;
$forum-card-inner-border-radius:          $card-inner-border-radius !default;
$forum-card-cap-padding-y:                $card-cap-padding-y !default;
$forum-card-cap-padding-x:                $card-cap-padding-x !default;
$forum-card-cap-bg:                       $card-cap-bg !default;
$forum-card-cap-color:                    $card-cap-color !default;
$forum-card-height:                       $card-height !default;
$forum-card-color:                        $card-color !default;
$forum-card-bg:                           $card-bg !default;
$forum-card-img-overlay-padding:          $card-img-overlay-padding !default;
$forum-card-group-margin:                 $card-group-margin !default;

$news-card-spacer-y:                     $card-spacer-y !default;
$news-card-spacer-x:                     $card-spacer-x !default;
$news-card-title-spacer-y:               $card-title-spacer-y !default;
$news-card-border-width:                 $card-border-width !default;
$news-card-border-radius:                $card-border-radius !default;
$news-card-border-color:                 $card-border-color !default;
$news-card-inner-border-radius:          $card-inner-border-radius !default;
$news-card-cap-padding-y:                $card-cap-padding-y !default;
$news-card-cap-padding-x:                $card-cap-padding-x !default;
$news-card-cap-bg:                       $card-cap-bg !default;
$news-card-cap-color:                    $card-cap-color !default;
$news-card-height:                       $card-height !default;
$news-card-color:                        $card-color !default;
$news-card-bg:                           $card-bg !default;
$news-card-img-overlay-padding:          $card-img-overlay-padding !default;
$news-card-group-margin:                 $card-group-margin !default;
